import React, { useEffect } from "react";
import {Navbar, Nav} from "react-bootstrap";
import Container from 'react-bootstrap/Container'
import {useState} from "react";

// for the container not found issue:https://github.com/react-bootstrap/react-bootstrap/issues/6350
// Change react-bootstap version to v1.6.6 in package.json

export default function TopNav() {


  const [activeLink, setActiveLink] = useState('home');
  const [scrolled, setScrolled] = useState(false);

  useEffect(()=>{
    const onScroll = () => {
      if (window.scrollY > 50){
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const onUpdateActiveLink = (link) => {
    setActiveLink(link);
  }


  return (
    // doc: https://react-bootstrap.github.io/components/navbar/
    <Navbar expand="lg" bg="light" className ={scrolled ? "scrolled": ""}>
      <Container>
        <Navbar.Brand href="#home">[HappyDoggie666]
          {/* Todo:update this */}
          <img src={''} alt="logo" />
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="basic-navbar-nav" >
          <span className="navbar-toggler-icon"></span>
        </Navbar.Toggle>

        {/* <Navbar.Toggle aria-controls="basic-navbar-nav" /> */}
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#home" className={activeLink === 'home' ? 'active navbar-link': 'navbar-link'} onClick = {() => onUpdateActiveLink('home')}>
              Home
            </Nav.Link>
            <Nav.Link href="#project" className={activeLink === 'project' ? 'active navbar-link': 'navbar-link'} onClick = {() => onUpdateActiveLink('project')}>
              Project
            </Nav.Link>
            <Nav.Link href="#skills" className={activeLink === 'skills' ? 'active navbar-link': 'navbar-link'} onClick = {() => onUpdateActiveLink('skills')}>
              Skills
            </Nav.Link>
            <Nav.Link href="#contact" className={activeLink === 'contact' ? 'active navbar-link': 'navbar-link'} onClick = {() => onUpdateActiveLink('contact')}>
              Contact
            </Nav.Link>
          </Nav>

          <span className="navbar-text">
            <div className="social-icon">
              <a href="#"><img src={''} alt=""/></a>
              <a href="#"><img src={''} alt=""/></a>
              <a href="#"><img src={''} alt=""/></a>
            </div>

            <button className="vvd" onClick={() => console.log('connect')}><span>Let's Connect</span></button>
          </span>

        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
  
}